<template>
  <v-layout row>
    <v-flex xs12 sm12 >
      <v-card>
        <v-toolbar color="cyan" dark>
          <v-toolbar-side-icon></v-toolbar-side-icon>

          <v-toolbar-title>Liste der Farantoevents</v-toolbar-title>

          <v-spacer></v-spacer>

          <v-btn icon>
            <v-icon>search</v-icon>
          </v-btn>
        </v-toolbar>
        <v-list two-line>
          <template v-for="(item, index) in items">
            
            <v-divider v-if="index + 1 < items.length" :key="`divider-${index}`"></v-divider>

            <v-list-tile             
              :key="item.title"
              avatar              
            >
              <v-list-tile-avatar>
                <img :src="item.image">
              </v-list-tile-avatar>

              <v-list-tile-content>
                <v-list-tile-title v-html="item.text"></v-list-tile-title>
                <v-list-tile-sub-title v-html="item.subtitle"></v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
          </template>
        </v-list>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  computed:{
      items(){
          return this.$store.getters['Events/getEvents']
      }
  }
}
</script>

